@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

body{
    margin: 0 auto;
    background: #0575E6; 
    background: -webkit-linear-gradient(to bottom, #021B79, #0575E6);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #021B79, #0575E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.main-wrap {
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
}
.calc-box{
    width: 280px;
    text-align: center;
    padding: 10px;
    border-radius: 8px;
    background-color: #fff;
    filter: drop-shadow(0px 2px 3px rgba(210,213,214,0.7));
}

.keypad-box {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
    width: 256px;
    padding: 10px;
    font-size: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    height: 40px;
    margin-bottom: 10px;
}
input[type="text"]:focus {
    outline: none;
}
button {
    font-size: 18px;
    font-family: 'Inter', sans-serif;
    height: 60px;
    border: none;
    margin: 5px;
    border-radius: 50%;
}

button:focus {
    outline: none;
}
.numbers {
    border: none;
    margin: 5px;
    border-radius: 50%;
    background-color: #fff;
}
.oprators {
    border: none;
    margin: 5px;
    border-radius: 50%;
}

#result{
    grid-column: 3/5;
    grid-row: 5;
    background-color: #2372db;
    border: none;
    border-radius: 30px;
    color: #fff;
}